.wrapper {
  position: absolute;
  width: 100%;
  height: 100vh;

  display: flex;
  top: 0px;
  left: 0px;
  z-index: 1000;
}

.loading {
  margin: auto;
  padding-left: 5px;
  width: 80px;
  height: 80px;

  display: flex;
  align-items: center;
  justify-content: center;

  border: 50%;
  background: rgba(255,255,255 0.5);
}