.header {  
  position: fixed;
  top: 0;
  width: 100%;
  padding: 0.5rem 0;
  z-index: 100;

  background-color: #FFF;
  box-shadow: 0px 1px 1px rgba(0,0,0, 0.1);
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  padding: 0.5rem 0;
}

.login {
  color: #333;
  display: flex;
  align-items: center;
}

.login::after {
  content: '';
  display: inline-block;
  width: 14px;
  height: 17px;
  background: url('../../assets/usuario.svg') no-repeat center center;
  margin-left: 0.5rem;
  position:relative;
  top: -1px;
}